.button {
    width: 52.4vw;
    /* background: #F64c72; */
    margin: 0 auto;
    text-align: right;
    margin-top: 40px;
}

body .button{
    margin-top:40px;
}

.button input[type="button"] {
    background: #F64c72;
    border: 0;
    padding: 8px 25px;
    font-size: 15px;
    letter-spacing: 2px;
}

.heading {
    font-size: 20px;
    color:#fff;
    margin-bottom: 20px;
    letter-spacing: 2px;
    font-weight: bolder;
    text-transform: uppercase;
}

.addOnTitle {
    width: 80%;
    display: inline-block;
    text-align: left;
}

.addOncontrols {
    display: inline-block;
    width: 20%;
}

.button input[type="button"]:first-child {
    margin-left: 0px;
}

.container {
    display: block;
    width: 920px;
    margin: 0 auto;
}
div#root hr.totalHr {
    width:100vw;
    max-width:50vw;
}

.childContainer h1 {
    text-align: left;
    font-size: 1.54vw;
    text-transform: uppercase;
    letter-spacing: 1.2px;
        font-weight: 500;
}


input[type="checkbox"] {
    margin-left: 3.5vw;
    width: 15px;
    height: 15px;
    border: 0.5px solid;
    background-color: transparent;
    border-radius: 5px;
}

div.path-style {
    width: 100%;
    height: 500px;
    background-color: red;
}